import React, { FC, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Card, Col, Progress, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, WindowComponent } from 'summer';
import { CLASS_NAME, PagamentoCooperativa } from '../../../../models/dtos/mfp/pagamentoCooperativa';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

interface PagamentoCooperativaEditViewProps extends EditViewProps<PagamentoCooperativa> {}

const PagamentoCooperativaEditView: FC<PagamentoCooperativaEditViewProps & RouteComponentProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const selectedItem = getSelectedItem(props);
  const empresaMFPDTO = globalReducer.globalParameter.empresaMFP;
  const formProps = useForm<PagamentoCooperativa>({
    defaultValues: { empresaMFP: { id: empresaMFPDTO.id }, aliquota: 0, valor: 0, ...selectedItem },
    mode: 'onBlur',
  });

  const [isValorAliquota, setIsValorAliquota] = useState(0);

  const calcularValorAliquota = () => {
    setIsValorAliquota(formProps.getValues().valor * (formProps.getValues().aliquota / 100));
  };

  useEffect(
    () => {
      setTimeout(calcularValorAliquota, 700);
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  return (
    <WindowComponent title="Manutenção de Pagamento à Cooperativa">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <Row>
          <Col>
            <InputGroupInline hidden={true} formProps={formProps} id="empresaMFP.id" />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              id="referencia"
              isPrependLabel={true}
              label="Referência"
              placeholder="Informe a Referência"
              type={InputType.REFERENCE}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={formProps}
              id="valor"
              isPrependLabel={true}
              label="Valor"
              placeholder="Informe o valor."
              onChange={calcularValorAliquota}
              type={InputType.NUMBER}
            />
          </Col>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { min: 0, max: 100 } }}
              id="aliquota"
              isPrependLabel={true}
              label="Alíquota"
              placeholder="Informe a Alíquota."
              onChange={calcularValorAliquota}
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <Card className="card-box mb-3">
              <Col className="p-10">
                <div className="divider-v divider-v-lg" />
                <div className="d-flex align-items-center pb-4 justify-content-between">
                  <div>
                    <div className="font-weight-bold">Alíquota</div>
                    <span className="text-black-50 d-block">Valor a recolher</span>
                  </div>
                  <div className="display-4 font-weight-bold text-info">
                    {isValorAliquota.toLocaleString('pt-br', { style: 'currency', currency: 'BRL' })}
                  </div>
                </div>
                <div>
                  <Progress className="progress-sm progress-bar-rounded" color="info" value={formProps.getValues().aliquota} />
                  <div className="align-box-row progress-bar--label mt-2 text-muted">
                    <div className="ml-auto">100%</div>
                  </div>
                </div>
              </Col>
            </Card>
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default PagamentoCooperativaEditView;
